@import "./index.scss";
.profile{
    &-title{
        border-bottom: 1px solid $border-grey;
        padding: 15px;
    }
    h3{
        font-size: 20px;
        margin-bottom: 0;
        font-weight: bold;
    }
    &-tweets{
        h3{
            padding: 15px;
            font-size: 18px;
            border-bottom: 1px solid $border-grey;
        }
        > .btn.btn-primary{
            display: flex;
            margin: 0 auto;
            margin-top: 10px;
            margin-bottom: 10px;
            background-color: transparent;
            border: 0;
            &:hover,
            &:focus{
                background-color: transparent !important;
                color: $primary;
            }
        }
    }
}